<template>
  <div class="home-page">
    <h1>AI辅助就医问诊引擎</h1>
    <div class="chat-container">
      <div v-for="(message, index) in internalMessages" :key="index" :class="['chat-message', message.sender]">
        <img :src="message.avatar" class="avatar" />
        <div class="message-content">{{ message.text }}</div>
      </div>
    </div>
    <div class="input-container">
      <input v-model="userInput" @keyup.enter="sendMessage" placeholder="请输入您的问题..." />
      <button @click="sendMessage">发送</button>
    </div>
  </div>
</template>

<script>
export default {
  props: ['messages'],
  data() {
    return {
      internalMessages: [], // 内部消息记录
      userInput: '',
    };
  },
  watch: {
    messages: {
      immediate: true,
      handler(newMessages) {
        this.internalMessages = [...newMessages]; // 将 props 的消息复制到内部数据
      },
    },
  },
  methods: {
    sendMessage() {
      if (this.userInput.trim()) {
        const userMessage = {
          text: this.userInput,
          sender: 'user',
          avatar: '/path/to/user-avatar.png',
        };
        this.internalMessages.push(userMessage);
        this.userInput = '';

        // 模拟AI回复
        setTimeout(() => {
          const botMessage = {
            text: `这是对“${userMessage.text}”的自动回复。`,
            sender: 'bot',
            avatar: '/path/to/bot-avatar.png',
          };
          this.internalMessages.push(botMessage);
          this.$emit('update-messages', this.internalMessages); // 将消息传递给父组件
        }, 1000);
      }
    },
  },
};
</script>

<style>
.home-page {
  flex: 1;
  padding: 2em;
}

h1 {
  text-align: center;
  color: #3f51b5;
}

.chat-container {
  border: 1px solid #ddd;
  padding: 1em;
  height: 400px;
  overflow-y: auto;
  background-color: #f9f9f9;
}

.chat-message {
  display: flex;
  margin-bottom: 1em;
  align-items: flex-end;
}

.chat-message.user {
  flex-direction: row-reverse;
  text-align: right;
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 0 1em;
}

.message-content {
  max-width: 70%;
  padding: 0.5em 1em;
  border-radius: 8px;
  background-color: #e0e0e0;
}

.chat-message.user .message-content {
  background-color: #3f51b5;
  color: white;
}

.input-container {
  display: flex;
  max-width: 600px;
  margin: 1em auto;
}

input {
  flex: 1;
  padding: 0.5em;
  font-size: 1em;
  border: 1px solid #ddd;
  border-radius: 4px 0 0 4px;
}

button {
  padding: 0.5em 1em;
  font-size: 1em;
  border: none;
  background-color: #3f51b5;
  color: white;
  cursor: pointer;
  border-radius: 0 4px 4px 0;
}
</style>
